.portfolio {
  width: 100%;
  background: #FFD600;
}

/* Smooth scroll behavior */
html {
  scroll-behavior: smooth;
}

/* Section base styles */
section {
  padding: 80px 20px;
  max-width: 100%;
  margin: 0 auto;
}

@media (max-width: 768px) {
  section {
    padding: 60px 16px;
  }
}